import {useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { BiUser } from 'react-icons/bi';
import { RiLockPasswordLine } from 'react-icons/ri';
import { useNavigate } from 'react-router-dom';
import {LoginApi} from 'utils/Api'

const Login = () => {
  const navigate = useNavigate();
	const {t} = useTranslation()
	const [userForm, setUserForm] = useState({
		phone: '',
		passwd: '',
	})
	
	useEffect(() => {
		localStorage.removeItem('__web_token')
	},[])

	const handleInput = (e) => {
		if(e.target.name === 'phone'){
			if(e.target.value.length <= 11){
				setUserForm({...userForm, [e.target.name]: e.target.value.replace(/[^0-9]/ig,'')})
			}
		}else{
			setUserForm({...userForm, [e.target.name]: e.target.value})
		}

	}

  const handleClick = (e) => {
    e.preventDefault();
		LoginApi(userForm).then((res)=> {
			localStorage.setItem('__web_token', res.data.token)
			navigate('/person')
		})
  };
  return (
    <div className="h-screen flex flex-col justify-center items-center  gap-4 bg-teal-300">
      <div className="w-24 h-24 rounded-full shadow flex justify-center items-center border-2 border-solid border-orange-500 bg-white">
        logo
      </div>
      <div className="flex  w-11/12 h-10 rounded-md bg-white justify-start items-center ">
        <BiUser className="ml-2 mr-2" />
        <input
          className="outline-none w-full"
          type="text"
					name='phone'
					value={userForm.phone}
          placeholder={t('phone_number')}
					onChange={handleInput}
        ></input>
      </div>

      <div className="flex bg-white justify-start items-center w-11/12 h-10 rounded-md">
        <RiLockPasswordLine className="ml-2 mr-2" />
        <input
          type="password"
					name='passwd'
					value={userForm.passwd}
          className="w-full outline-none"
          placeholder={t('login_pwd')}
					onChange={handleInput}
        ></input>
      </div>

      <button
        className="w-10/12 h-12 rounded-xl bg-white mt-4 font-bold text-orange-600"
        onClick={handleClick}
      >
        {t('login_now')}
      </button>
      <button
        className="w-10/12 h-12 rounded-xl border-2 border-solid font-bold text-white"
        onClick={() => {
          navigate('/register');
        }}
      >
        {t('reg_now')}
      </button>
    </div>
  );
};

export default Login;
